import React, { useEffect, useState } from 'react';
import { Button, Card, CardBody, CardGroup, Col, Container, Row } from 'reactstrap';
import './scanPodId.scss'
import { AWS } from '../../Services/AuthService';
import { useNavigate } from 'react-router-dom';
import { GET_CURRENT_POD, GET_POD_INFO } from '../../GraphQL/Queries';
import { useQuery } from '@apollo/client';
import '../ScanPodId/podInfo.scss'
import moment from 'moment/moment';
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';
import useGraphQL from '../../GraphQL/GraphQL';
import PopupMenu from '../../Components/Popup';
import {
    COMMAND_RESPONSE_UUID,
    COMMAND_UUID,
    INFORMATION_UUID,
    SKIIN_SERVICE_UUID,
    STORED_METRICS_UUID
} from "../../Utlis/constant";
import {useGetUserGroups} from '../../Hooks/useGetUserGroups';
import { useGetCurrentPod } from '../../Hooks/useGetCurrentPod';

function PodInfo() {
    const aws = AWS.getInstance();
    const arrow = require('../../Assests/arrow_back.png');
    const groupID = localStorage.getItem('clinicId');
    const podID = localStorage.getItem("podId");
    const [clinicName, setClinicName] = useState('');
    const { userGroups, getUserGroups } = useGetUserGroups();
    const { currentPod, getCurrentPod } = useGetCurrentPod();
    const [readPodText, setReadPodText] = useState('Connect and Read Pod Data');
    const [isPodUnlocked, setPodUnlocked] = useState(false)

    useEffect(() => {
        getUserGroups().then();
    }, [])

    // useEffect(() => {
    //     if(userGroups.length) {
    //         console.log(userGroups);
    //         getCurrentPod({ groupId: userGroups[0].groupId, podId: '021035401157'}).then();
    //     }
    // }, [userGroups])

    // useEffect(() => {
    //     console.log(currentPod);
    // }, [currentPod])

    const navigate = useNavigate();
    const { makeAPIQueryWithOptions } = useGraphQL();
    const [result, setResult] = useState('');

    useEffect(() => {
        const name = localStorage.getItem("clinicName")
        setClinicName(name)
        fetchPodInfo();
    }, []);

    const fetchPodInfo = async () => {
       
        try {
            const response = await makeAPIQueryWithOptions('getCurrentPod', GET_CURRENT_POD, {
                groupId: groupID, podId: podID.slice(1, 12)
              });
              const result = response?.data
              return setResult(result)
        } catch (ex) {
          console.log('getCurrentPod Summary', ex);
        }
      };


    const timestamp = localStorage.getItem("timestamp");

    // get serial number from podID which is populated from ScanPodId page
    let serialNo = podID.slice(1, 12)
    let serialNoToString = serialNo.toString()


    const connectPod = () => {
        var date = new Date(parseInt(timestamp));
        return moment(date).format('YYYY-MM-DD');
    }

    // useEffect(() => {
    //     if(userGroups.length) {
    //         console.log(userGroups);
    //         getCurrentPod({ groupId: userGroups[0].groupId, podId: '081031600203'}).then();
    //     }
    // }, [userGroups])


    useEffect(() => {
        const id = localStorage.getItem("clinicId")
        console.log('id = ', id)
        console.log('groupID = ', groupID)
        
        if (id) {
            const fileteredClinic = aws.getGroups.filter(clinic => clinic.groupId === id);
            setClinicName(fileteredClinic[0]?.groupName)
        };
    }, []);

    // const { data } = useQuery(GET_CURRENT_POD, {
    //     variables: { groupId: groupID, podId: podID.slice(1, 12) }
    // });
    // console.log("data", data);
    // console.log('data?.getCurrentPod.message = ', data?.getCurrentPod.message)


    const displayTime = (timestamp) => {

        var date = new Date(parseInt(timestamp));
        return moment(date).format('YYYY-MM-DD');
    }

    const back = () => {
        navigate("/scan-pod-id")
    }

    const viewHistory = () => {
        navigate("/pod-history");
    }


    const onPressReadPod = () => {


        const parseInformationCharacteristic = (data) => {
            let firmwareVersion = {
              major: data[0] >>> 0,
              minor: data[1] >>> 0,
              patch: (data[2] | (data[3] << 8)) >>> 0,
            }
            let hardwareVersion = (data[4] | (data[5] << 8));

            let ecgLeadState = Boolean(data[6] & 1);  // Adjusted index

            let batteryLevel = data[8];

            let storedData = Boolean(data[9]);

            let metricsSegCount = (data[10] | (data[11] << 8));

            let ecgSegCount = (data[12] | (data[13] << 8));

            let chargingState = data[14];

            let connectionInterval = (data[15] | (data[16] << 8));

            let temperature = (data[17] | (data[18] << 8));

            let movementFlag = Boolean(data[19]);
    
            return {
              firmwareVersion,
              hardwareVersion,
              ecgLeadState,
              batteryLevel,
              storedData,
              metricsSegCount,
              ecgSegCount,
              chargingState,
              connectionInterval,
              temperature,
              movementFlag,
            }
        }

        console.log('Connecting to Pod');

        // API call for getting Pod Unlock key
        const getPodInfo = async (serialNoToString, iv, secret ) => {
            const fetchPolicy = 'network-only'           
            const response = makeAPIQueryWithOptions('getPodInfo ', GET_POD_INFO, 
                {
                  podId: serialNoToString,
                  IV: iv,
                  secretNumber: secret
                },
                fetchPolicy);
            return response
        }

        let unlockCommand, responseArray; 

        // Receive notifications from Command Response. 
        // Will get secret and IV from this response. 
        // Secret and IV get sent as inputs to getPodInfo API call
        function handleCharacteristicValueChanged(event) {
            const value = event.target.value;
            responseArray = new Uint8Array(value.buffer);
            console.log('Received notification:', responseArray.toString());
            let unlockRequest = Array.from(responseArray.slice(0, 3));

            // Helper function
            function arraysEqual(a, b) {
                if (a.length !== b.length) return false;
                for (let i = 0; i < a.length; i++) {
                  if (a[i] !== b[i]) return false;
                }
                return true;
            }

            // If unlockRequest is [148, 237, 0], then Pod is unlocked
            if (arraysEqual(unlockRequest, [148, 237, 0])) {
                setPodUnlocked(true)
                console.log('Pod is Unlocked = ', isPodUnlocked);
            }

            if (arraysEqual(unlockRequest, [4, 153, 6])) {
                
                console.log('Pod is already unlocked');
                console.log('Disconnecting from BlueTooth Device');
                server.disconnect();
            }

            
            if (arraysEqual(unlockRequest, [4, 153, 0])) {
                console.log('Unlocking Pod with secret and IV');
                const secret1 = responseArray.subarray(3, 11); 
                const numericSecret = Array.from(secret1);
                const iv = responseArray.subarray(11, 27);
                const numericIV = Array.from(iv);
                
                // Secret and IV get sent as inputs to getPodInfo API call
                getPodInfo(serialNoToString, numericIV, numericSecret).then((response) => {
                    const resultSecret = response.data.getPodInfo.encryptedSecretNumber;
                    const resultIV = response.data.getPodInfo.IV;

                    // Use outputs from getPodInfo API call to build the unlockCommand key
                    const UNLOCK_POD_OPCODE = [148, 237];
                    unlockCommand = new Uint8Array(UNLOCK_POD_OPCODE.concat(resultSecret, resultIV));
                    console.log('unlockCommand = ', unlockCommand)
                })
            }
        }

        // Check if unlockCommand is populated
        function waitForUnlockCommand() {
            return new Promise(resolve => {
                const checkInterval = setInterval(() => {
                    if (unlockCommand) {
                        clearInterval(checkInterval);
                        resolve();
                    }
                }, 100); // Check every 100ms
            });
        }

        let device, server, service, writeCharacteristic, storedMetricsCharacteristic, informationCharacteristic;

        // Scan for BlueTooth Devices containing the Skiin Service UUID
        navigator.bluetooth.requestDevice({
            filters: [{ services: [SKIIN_SERVICE_UUID] }],
            optionalServices: [COMMAND_UUID, COMMAND_RESPONSE_UUID, 
                               INFORMATION_UUID, STORED_METRICS_UUID]
        })
        // Connect to selected Device.
        .then(selectedDevice => {
            device = selectedDevice;
            return device.gatt.connect();
        })
        .then(connectedServer => {
            server = connectedServer;
            return server.getPrimaryService(SKIIN_SERVICE_UUID);
        })
        .then(service => {
            // Get the characteristic for notifications
            return service.getCharacteristic(COMMAND_RESPONSE_UUID);
          })
        .then(characteristic => {
            // Add an event listener to handle notifications
            characteristic.addEventListener('characteristicvaluechanged', handleCharacteristicValueChanged);
            return characteristic.startNotifications();
        })
        .then(characteristic => {
            console.log(`Subscribed to notifications for ${characteristic.uuid}`);
            // Now, get the characteristic for writing
            writeCharacteristic = characteristic.service.getCharacteristic(COMMAND_UUID);
            return writeCharacteristic
        })
        .then(writeCharacteristic => {
            // Get Secret and IV 
            const getSecret = new Uint8Array([4, 153])
            console.log('getSecret = ', getSecret)
            writeCharacteristic.writeValue(getSecret)            
            return writeCharacteristic
        })
        .then(async writeCharacteristic => {
            const readStoredData = new Uint8Array([0, 153]) // Read stored data
            await waitForUnlockCommand();
            console.log('writing unlockCommand = ', unlockCommand);
            await writeCharacteristic.writeValue(unlockCommand);
            // Show current data existing on the pod
            console.log('Read Stored Data = ', readStoredData);
            await writeCharacteristic.writeValue(readStoredData);
            return server.getPrimaryService(SKIIN_SERVICE_UUID)
            .then(service => service.getCharacteristic(INFORMATION_UUID));
        })
        .then(async informationCharacteristic => {
            const infoValue = await informationCharacteristic.readValue();
            const podInformation = new Uint8Array(infoValue.buffer)
            console.log('Information:', podInformation);
            // Parse the data
            const parsedData = parseInformationCharacteristic(podInformation);
            // Convert parsed data to string
            const formattedData = JSON.stringify(parsedData, null, 2);
            // Display data inside button text
            setReadPodText(`Pod Data from INFORMATION_UUID: {${formattedData}}`);
            // Disconnect from BlueTooth Device
            console.log('Disconnecting BlueTooth Device');
            await server.disconnect();
        })
    }

    const onPressDisassociate = () => {
        console.log('Connecting to Pod');

        // API call for getting Pod Unlock key
        const getPodInfo = async (serialNoToString, iv, secret ) => {
            const fetchPolicy = 'network-only'           
            const response = makeAPIQueryWithOptions('getPodInfo ', GET_POD_INFO, 
                {
                  podId: serialNoToString,
                  IV: iv,
                  secretNumber: secret
                },
                fetchPolicy);
            return response
        }

        let unlockCommand, responseArray; 

        // Receive notifications from Command Response. 
        // Will get secret and IV from this response. 
        // Secret and IV get sent as inputs to getPodInfo API call
        function handleCharacteristicValueChanged(event) {
            const value = event.target.value;
            responseArray = new Uint8Array(value.buffer);
            console.log('Received notification:', responseArray);
            let unlockRequest = Array.from(responseArray.slice(0, 3));

            // Helper function
            function arraysEqual(a, b) {
                if (a.length !== b.length) return false;
                for (let i = 0; i < a.length; i++) {
                  if (a[i] !== b[i]) return false;
                }
                return true;
            }

            // If unlockRequest is [148, 237, 0], then Pod is unlocked
            if (arraysEqual(unlockRequest, [148, 237, 0])) {
                setPodUnlocked(true)
                console.log('Pod is Unlocked = ', isPodUnlocked);
            }
            
            if (arraysEqual(unlockRequest, [4, 153, 0])) {
                console.log('Unlocking Pod with secret and IV');
                const secret1 = responseArray.subarray(3, 11); 
                const numericSecret = Array.from(secret1);
                const iv = responseArray.subarray(11, 27);
                const numericIV = Array.from(iv);
                
                // Secret and IV get sent as inputs to getPodInfo API call
                getPodInfo(serialNoToString, numericIV, numericSecret).then((response) => {
                    const resultSecret = response.data.getPodInfo.encryptedSecretNumber;
                    const resultIV = response.data.getPodInfo.IV;

                    // Use outputs from getPodInfo API call to build the unlockCommand key
                    const UNLOCK_POD_OPCODE = [148, 237];
                    unlockCommand = new Uint8Array(UNLOCK_POD_OPCODE.concat(resultSecret, resultIV));
                    console.log('unlockCommand = ', unlockCommand)
                })
            }
        }

        // Check if unlockCommand is populated
        function waitForUnlockCommand() {
            return new Promise(resolve => {
                const checkInterval = setInterval(() => {
                    if (unlockCommand) {
                        clearInterval(checkInterval);
                        resolve();
                    }
                }, 100); // Check every 100ms
            });
        }

        let device, server, service, writeCharacteristic, responseCharacteristic;

        // Scan for BlueTooth Devices containing the Skiin Service UUID
        navigator.bluetooth.requestDevice({
            filters: [{ services: [SKIIN_SERVICE_UUID] }],
            optionalServices: [COMMAND_UUID, COMMAND_RESPONSE_UUID, INFORMATION_UUID]
        })
        // Connect to selected Device.
        .then(selectedDevice => {
            device = selectedDevice;
            return device.gatt.connect();
        })
        .then(connectedServer => {
            server = connectedServer;
            return server.getPrimaryService(SKIIN_SERVICE_UUID);
        })
        .then(service => {
            // Get the characteristic for notifications
            return service.getCharacteristic(COMMAND_RESPONSE_UUID);
          })
        .then(characteristic => {
            // Add an event listener to handle notifications
            characteristic.addEventListener('characteristicvaluechanged', handleCharacteristicValueChanged);
            return characteristic.startNotifications();
        })
        .then(characteristic => {
            console.log(`Subscribed to notifications for ${characteristic.uuid}`);
            // Now, get the characteristic for writing
            writeCharacteristic = characteristic.service.getCharacteristic(COMMAND_UUID);
            return writeCharacteristic
        })
        .then(writeCharacteristic => {
            // Get Secret and IV 
            const getSecret = new Uint8Array([4, 153])
            console.log('getSecret = ', getSecret)
            writeCharacteristic.writeValue(getSecret)            
            return writeCharacteristic
        })
        .then(async writeCharacteristic => {
            const eraseMetrics = new Uint8Array([2, 144, 0]) // Erase stored data metrics
            const eraseECG = new Uint8Array([2, 144, 1]) // Erase stored data ECG
            const readStoredData = new Uint8Array([0, 153]) // Read stored data
            await waitForUnlockCommand();
            console.log('writing unlockCommand = ', unlockCommand);
            await writeCharacteristic.writeValue(unlockCommand);
            console.log('writing Erase Command metrics = ', eraseMetrics);
            await writeCharacteristic.writeValue(eraseMetrics);
            console.log('writing Erase Command ecg = ', eraseECG);
            await writeCharacteristic.writeValue(eraseECG);   
            console.log('Read Stored Data = ', readStoredData);
            // Show current data existing on the pod
            await writeCharacteristic.writeValue(readStoredData);
            return writeCharacteristic
        })
        .then(async writeCharacteristic => {
            const readStoredData = new Uint8Array([0, 153]) // Read stored data
            await writeCharacteristic.writeValue(readStoredData); 
            await server.disconnect();
            console.log('Disconnecting BlueTooth Device');
        })
        .catch(error => {
            console.error('Error:', error);
        });
    }


    return (
        <>
            <div className="app flex-row align-items-center">
                <Container className='form-container'>
                    <Row >
                        <Col md="5" className='container-width'>
                            <div style={{ display: 'flex', marginTop: "50px", marginBottom: "30px" }}>
                                <div>
                                    <Button className='back-btn' onClick={back}>
                                    <img alt="back-arrow" className='back-arrow-img' src={arrow} />
                                    Back
                                    </Button>
                                </div>
                                <div className='clinic-div'><p className='clinic-name-text'> {clinicName} </p></div>
                                <div><Button className='logput-btn' onClick={() => {aws.logout()}}>Log out</Button></div>
                            </div>
                            <CardGroup>
                                <Card className='card-container'>
                                    <CardBody className='card-body'>
                                        <div className='data-div'>
                                            <div className='pod-id-div'>Pod ID {podID}</div>
                                            {result?.getCurrentPod?.message === 'The pod is associated with a patient who is not a part of the clinic' ?
                                                <div className='message-div'>This Pod is associated with a patient who is not a part of your clinic. Please contact your account representative for support.</div>
                                                :
                                                result?.getCurrentPod?.message === 'The pod is currently not associated with a patient' ?
                                                    <div className='message-div'>This Pod is currently not associated with a patient. You may use the Skiin Connected Life application to associate it with a patient.</div>
                                                    :
                                                    <div>
                                                        <div className='associated-div'>Associated with:<span className='associated-text'> {result?.getCurrentPod?.data.name}</span>
                                                        </div>
                                                        <div className='date-div'>Associated: <span className='associated-text'> {displayTime(result?.getCurrentPod?.data.timestamp)}</span></div>
                                                    </div>
                                            }
                                        </div>
                                        <Button
                                            onClick={onPressReadPod}
                                            type="button"
                                            color="primary"
                                            className="px-4 clear-data-btn big-button"
                                        >
                                            {readPodText}
                                        </Button>
                                        <Popup
                                            className='pop-up-container'
                                            trigger={<Button
                                                // onClick={onPressDisassociate}
                                                type="button"
                                                color="primary"
                                                className="px-4 clear-data-btn"
                                                disabled={(result?.getCurrentPod?.message === 'The pod is associated with a patient who is not a part of the clinic') || result?.getCurrentPod?.message === 'The pod is currently not associated with a patient' ? true : false}
                                            >
                                                Clear data and disassociate Pod
                                            </Button>
                                            }
                                            modal
                                            nested
                                        >
                                            {close => (
                                                <PopupMenu close = {(reloadPodList = false)=> {
                                                    if(reloadPodList)
                                                        fetchPodInfo();
                                                    close();
                                                }}></PopupMenu>                         
                                            )}

                                        </Popup>

                                        <Button
                                            onClick={viewHistory}
                                            type="button"
                                            color="primary"
                                            className="px-4 view-history-btn"
                                        >
                                            View Pod history with your clinic
                                        </Button>
                                    </CardBody>
                                </Card>
                            </CardGroup>
                        </Col>
                    </Row>
                </Container>
            </div>
        </>
    );
}

export default PodInfo;